export default {
	data() {
		return {
			headerPosition:"fixed",
			key:'',
			historyList:[],
			hotList:['口罩','洗手液','喷雾','眼影','化妆棉','卸妆水','抽纸','杯子']
		}
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	methods: {
		toSearch(){
			uni.setStorage({
				key:'searchWord',
				data:this.key,
				success: (res) => {
					uni.getStorage({
						key: 'searchWord',
						data: res.data,
						success:(result)=>{
							console.log(result.data);
							this.historyList.push(result.data)
						}
					});
				},
				fail: (e) => {
					console.log('error:'+JSON.stringify(e))
				}
			})
		},
		del(){
			this.historyList=[];
		}
	}
}
